.skills-container {
  width: 80vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;

  h1 {
    font-size: 3rem;
    line-height: 20px;
  }

  h2 {
    font-size: 1.2rem;
    line-height: 0px;
    font-weight: normal;
    color: lightgrey;
    padding-bottom: 35px;
    border-bottom: 3px solid darkorange;
    text-align: center;
    width: 80vw;
  }

  .skill-center{
    height: 30vh;
    width: 50vw;
  }

  .skill-text {
    text-align: center;
    margin-top: 15vh;
    font-size: 20px;
  }

  .skills-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 50px;
    grid-gap: 70px;

    .skill {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      opacity: .6;
      cursor: pointer;
      img {
        height: 8rem;
        border: 1px solid white;
      }
      p {
        font-size: 20px;
      }
      &:hover{
        opacity: 1;
        transform: scale(1.1);
      }
    }
  }
}